.main_text2 {
  width: 100%;
  height: 700px;
  background-color: green;
  background-image: url("../img/contact1.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #fff;
}

.main_text2 > ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.main_text2 > ul > li {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.main_text2 > ul > li > div,
.main_text2 > ul > li > div > h1 {
  color: #fff;
}

.main_text2 > ul > li > div > a {
  color: #fff;
}

.more2 {
  width: 220px;
  height: 40px;
  border: 1px solid #fff;
  color: #fff;
  line-height: 40px;
  cursor: pointer;
  margin: 16px auto;
  background-color: transparent;
  font-size: large;
}

@media (max-width: 768px) {
  .main_text2 {
    height: auto;
    padding: 20px;
  }

  .main_text2 > ul {
    flex-direction: column;
    align-items: center;
  }

  .main_text2 > ul > li {
    width: 100%;
    margin-bottom: 20px;
  }
}
