.start {
  opacity: 0;
}

.end {
  opacity: 1;
  transition: opacity 0.5s;
}

.main_bg {
  height: 100px;
  background-image: url('./img/bg.png');
  background-size: cover;
  background-position: center;
}

/* 기본 스타일 */
.main-images {
  width: 900px;
  margin: auto;
  display: flex;
  justify-content: space-around;
  padding: 20px;
}

.main-images img {
  cursor: pointer;
  width: 45%;
  transition: transform 0.3s ease-in-out;
}

.main-images img:hover {
  transform: scale(1.05);
}

/* 모바일 화면에서 적용될 스타일 */
@media (max-width: 768px) {
  .main-images {
    width: 100%; /* 컨테이너 너비를 조정합니다 */
    flex-direction: column; /* 이미지를 세로로 나열합니다 */
    align-items: center; /* 이미지를 중앙 정렬합니다 */
  }

  .main-images img {
    width: 80%; /* 이미지 너비를 조정합니다 */
    margin-bottom: 20px; /* 이미지 사이의 여백을 추가합니다 */
  }

  /* 모바일 화면에서 푸터 조정 */
  .footer {
    text-align: center; /* Center the text */
    display: flex;
    flex-direction: column; /* Arrange items in a column */
    align-items: center; /* Center items horizontally */
    justify-content: center; /* Center items vertically */
    background: #1f1f1f;
    padding: 15px;
    color: #BDBDBD;
    font-size: small;
  }

  .footer img {
    width: 80%; /* Adjust the width to be smaller */
    margin: 15px 0; /* Add some margin to space the image and text */
  }

  .footer div {
    color: #BDBDBD;
    text-align: center; /* Ensure text is center-aligned */
  }
}

.navbar-custom {
  background-color: #1f1f1f !important; /* !important를 사용하여 기본 Bootstrap 스타일을 덮어씁니다. */
}

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Prevent horizontal overflow */
}

.content-wrap {
  flex: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  background-color: #1f1f1f;
}

.App {
  background-color: rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1f1f1f;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main_text2 {
  width: 100%;
  height: 700px;
  background-image: url("../public/contact.jpg");
  background-position: center; /* 이미지를 수직 방향으로만 조정하려면 'center'만 지정 */
  background-size: cover;
  background-repeat: no-repeat;
  display: flex; /* Flexbox를 사용하여 자식 요소를 정렬 */
  align-items: center; /* 세로 방향 중앙 정렬 */
  justify-content: center; /* 가로 방향 중앙 정렬 */
  text-align: center; /* 텍스트 중앙 정렬 */
  color: #fff; /* 글자 색상을 흰색으로 설정 */
  position: relative; /* :before 요소를 위한 포지셔닝 컨텍스트 */
}

.main_text2 a {
  font-weight: bold;
  color: inherit; /* 링크 색상을 부모 요소의 색상과 동일하게 설정 */
  text-decoration: none; /* 밑줄 제거 */
}

.main_text2 a:hover {
  text-decoration: underline; /* 선택적으로 마우스 호버 시 밑줄을 추가할 수 있습니다 */
}

/* 오버레이 효과를 위한 :before 의사 요소 */
.main_text2:before {
  content: "";
  position: absolute; /* 부모 요소인 .main_text2의 상대 위치에 따라 배치 */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* 어두운 오버레이 색상 */
  z-index: 1; /* 텍스트보다 뒤에 오도록 설정 */
}

.main_text2 > ul {
  display: flex;
  position: relative; /* 오버레이 위에 텍스트가 오도록 포지셔닝 */
  z-index: 2; /* 오버레이 위에 텍스트가 오도록 설정 */
}

.more2 {
  width: 220px;
  height: 40px;
  border: 1px solid #fff;
  line-height: 40px;
  cursor: pointer;
  margin: 16px auto;
  background-color: transparent; /* 배경을 투명하게 설정 */
  font-size: large;
}

.footer {
  display: flex;
  justify-content: center; /* Center horizontally */
  background: #1f1f1f;
  padding: 15px;
  color: #BDBDBD;
  font-size: small;
}

.footer-container {
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center; /* Center the group horizontally */
  width: 100%; /* Ensure the container takes up the full width */
}

.footer img {
  width: 400px; /* Adjust the width as needed */
  margin-right: 13px; /* Space between image and text */
}

.footer div {
  color: #BDBDBD;
  text-align: left;
}

/* 모바일 화면에서 푸터 조정 */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer img {
    width: 300px; /* Adjust the width to be smaller */
    margin-bottom: 10px;
    margin-right: 0;
  }

  .footer div {
    text-align: center;
    white-space: pre-wrap; /* Ensure text wraps properly */
  }
}

/* Remove unnecessary white space */
body {
  padding: 0;
  margin: 0;
}
